import type { AccountData, CreateAccountData, GetAccountsData, UpdateAccountData } from '@strife/common';
import { AccountSchema } from '@strife/common';
import { injectable } from 'inversify';
import di from '../../app/di';
import type { IAccountService } from './account.service.interface.js';
import { AccountServiceServiceId } from './account.service.interface.js';
import { AuthenticationServiceServiceId } from './authentication.service.interface.js';
import BaseService from './base.service.js';

@injectable()
class AccountService extends BaseService implements IAccountService {

  protected override get baseUrl(): string | URL | undefined {
    return `http://localhost:3000/accounts`;
  }

  public async createAccount(data: CreateAccountData): Promise<AccountData> {
    const accountData = await this.post<AccountData>({ schema: AccountSchema, data });
    const authenticationService = await di.getAsync(AuthenticationServiceServiceId);
    await authenticationService.login({ email: data.email, password: data.password });
    return accountData;
  }

  public async deleteAccount(): Promise<void> {
    const authenticationService = await di.getAsync(AuthenticationServiceServiceId);
    if (!authenticationService.user)
      return Promise.reject();

    return this.delete({ url: `/${authenticationService.user.authentication.account_id}` });
  }

  public getAccount(id: string): Promise<AccountData> {
    return this.get<AccountData>({ schema: AccountSchema, url: `/${id}` });
  }

  public getAccounts(data: GetAccountsData): Promise<AccountData[]> {
    return this.get<AccountData[]>({ schema: AccountSchema.array(), data });
  }

  public async updateAccount(data: UpdateAccountData): Promise<AccountData> {
    const authenticationService = await di.getAsync(AuthenticationServiceServiceId);
    if (!authenticationService.user)
      return Promise.reject();

    return this.put<AccountData>({ schema: AccountSchema, url: `/${authenticationService.user.authentication.account_id}`, data });
  }

}

di.bind(AccountServiceServiceId).to(AccountService).inSingletonScope();